@charset "UTF-8";
//NHA-COMMON-RMA-025
.support_rma {
    .content {max-width:1200px; margin:170px auto 0;}
    .cont_title {width:100%; height:50px; position:fixed; top:120px; left:0; overflow:hidden; background-color:$white; z-index:1;}
    .title_wrap {
        max-width:1200px; height:50px; margin:0 auto;background-color:$white; overflow:hidden;
        border-bottom:2px solid $gray6;
        @include boxSizing;
        @extend %clearfix;
    }
    // left
    .title_box {
        float:left;
        p {
            padding:26px 0; font-weight:$medium;
            span { margin:0 5px 0 0; }
            &.default_title {
                font-size:16px;color:$gray6;font-weight:$medium;padding:22px 0 10px;
                span {color:$blue5;}
            }
        }
    }
    // right
    .control_wrap {
        float:right; padding:7px 0; font-size:0;
        > * {margin:0 0 0 20px;}
        .new_customer_btn {
            display:inline-block; line-height:32px; font-size:13px; font-weight:$medium; color:#121314; padding:0 20px; border:1px solid $gray3; vertical-align:middle;
            @include borderRadius(100px);
            &.new_customer_btn_type2 {
                background-color:$gray5; border-color:$gray5; color:$white;
            }
        }
    }
    .rma_list {
        padding:15px 0 40px; max-width:1200px; margin:0 auto;
        ol {
            @extend %clearfix;
        }
        li {
            width:calc(50% - 9px); margin:19px 0 0; float:left; border:1px solid $gray2; overflow:hidden;
            @include borderRadius(15px);
            @include boxSizing;
        }
        .img_wrap {
            position:relative; overflow:hidden;
            button {
                width:76px; height:76px; position:absolute; top:50%; left:50%; background-image:url(../../images/support/play_btn.png); z-index:1; text-indent:-9999px;
                @include translate(-50%, -50%);
                @include backgroundSoltC;
                &:hover,
                &:focus {
                    & ~ img {@include scale(1.15);}
                }
            }
            img {
                width:100%;
                @include transition;
            }
        }
        .txt_wrap {
            padding:30px 50px 25px; min-height:165px;
            a {
                font-size:26px; font-weight:$bold; color:$gray7;
                @include transition(color, .2s);
                &:hover,
                &:focus {color:$blue;}
            }
            .txt01 {font-size:16px; line-height:25px; color:$gray7; margin:20px 0 0;}
            .txt02 {
                font-size:14px; line-height:22px; color:$gray5; margin:12px 0 0; padding:12px 0 0; border-top:1px solid #e3e3e3;
            }
        }
    }
    @media screen and (min-width:1025px) {
        .rma_list {
            li {
                &:nth-of-type(2n) {float:right;}
                &:nth-of-type(2n+1) {clear:both;}
                &:nth-of-type(1), &:nth-of-type(2) {margin-top:0;}
            }
        }
    }
    @include mob() {
        .content {margin:em(386) 0 0;}
        .cont_title {
            margin:0; height:auto; position:fixed; top:em(256);
            @include boxSizing;
            &:after {width:100%; height:em(2);content:"";display:block;background-color:#e5e5e5;bottom:0; position:absolute;}
        }
        .title_wrap {
            width:100vw; height:em(110);min-height:em(130);max-width:inherit; padding:0 em(30); margin:0;
            border:none; overflow:hidden;
            @include boxSizing;
        }
        // left
        .title_box {
            padding:0 0 0 em(60);
            p {
                padding:em(50) 0; font-weight:$bold; line-height:em(45);
                span { margin:0 em(5) 0 0; }
                &.default_title {
                    font-size:em(32); padding:em(45) 0; font-weight:$bold;
                }
            }
            &.title_only {padding:0;}
        }
        // right
        .control_wrap {
            padding:em(28) 0;
            > * {margin:0 0 0 em(16);}
            .new_customer_btn {
                line-height:em(72); font-size:em(28);font-weight:$bold;padding:0 em(28);border:em(2) solid $gray3;
                @include borderRadius(em(100));
            }
        }
        .rma_list {
            padding:em(40) em(30);
            li {
                width:100%; margin:em(20) 0 0;
                @include borderRadius(1rem);
                &:first-child {margin:0;}
            }
            .img_wrap {
                button {width:76px; height:76px; background-image:url(../../images/support/play_btn.png);}
            }
            .txt_wrap {
                padding:em(60) em(55); min-height:auto;
                a {font-size:em(52);}
                .txt01 {font-size:em(32); line-height:em(42); margin:em(30) 0 0;}
                .txt02 {font-size:em(26); line-height:em(34); margin:em(30) 0 0; padding:em(30) 0 0;}
            }
        }
    }
}
//NHA-COMMON-RMA REVIEW-040
.order_review_wrap {
    max-width:1200px; margin:0 auto; box-sizing:border-box; padding:40px 0 100px;
    h3 {font-size:26px; padding:0 0 15px; color:$gray7; font-weight:$medium; border-bottom:2px solid $gray7;}
    .info {
        display:flex;
        p {
            font-size:14px; line-height:22px; color:$gray5; margin:0 45px 0 0;
            strong {display:inline-block; font-weight:$medium; background-color:#ebebeb; padding:0 12px; border-radius:20px; margin:0 5px 0 0;}
        }
    }
    .order_info {
        margin:40px 0 0;
        .box {
            padding:40px 0; border-bottom:1px solid $gray2;
            &:first-child {padding-top:0;}
            strong {font-size:26px; padding:0 0 25px; display:block; color:$gray7; font-weight:$medium;}
            dl {
                font-size:16px; line-height:22px; margin:20px 0 0;
                &:after {content:""; display:block; clear:both;}
                &:first-child {margin:0;}
                dt {width:280px; float:left; color:$gray5;}
                dd {width:calc(100% - 280px); float:left; color:$gray7}
            }
        }
    }
    .form_btn_wrap_long {padding:90px 0 0;}
    @include mob() {
        padding:em(80) em(30) em(100);
        h3 {font-size:em(36); padding:0 0 em(20); font-weight:$bold;}
        .info {
            display:block;
            p {
                display:block; font-size:em(26); line-height:em(44); margin:em(20) 0 0;
                &:first-child {margin:0;}
                strong {font-weight:$bold; padding:0 em(20); border-radius:em(40); margin:0 em(10) 0 0;}
            }
        }
        .order_info {
            margin:em(50) 0 0;
            .box {
                padding:em(50) 0;
                strong {font-size:em(32); line-height:em(44); padding:0 0 em(30); font-weight:$bold;}
                dl {
                    font-size:em(26); line-height:em(38); margin:em(30) 0 0;
                    dt {width:100%;}
                    dd {width:100%; margin:em(0) 0 0;}
                }
            }
            &.order_info2 {margin:em(50) 0 0;}
        }
        .form_btn_wrap_long {padding:em(100) 0 0;}
    }
}
//NHA-COMMON-ITEM SEARCH(P)-028
.popup.item_search_popup {
    .popup_content {
        max-width:1200px; background-color:$white; padding:50px 60px 70px; text-align:center; border-radius:30px;
        > strong {font-size:30px;font-weight:$medium;color:$gray7; display:block; padding:0 0 40px;}
        .cont {
            text-align:left; height:740px;
            max-height:calc(85vh - 60px - 90px - 70px);
            .search_wrap {
                padding:0 0 35px; border-bottom:1px solid $gray2;
                .tit_wrap {
                    margin:0 0 15px;
                    strong {font-size:20px; display:block; color:$gray7; font-weight:$medium;}
                }
                .box {
                    .form_list {
                        .form_date_type01 {width:246px; display:inline-block; vertical-align:middle}
                    }
                }
                .box_multi {
                    margin:40px 0 0; padding:0 126px 0 0; position:relative;
                    .multi_wrap {
                        &:after {content:""; display:block; clear:both;}
                        .box_s {
                            width:calc(33% - 12px); float:left; margin:0 0 0 12px;
                            &:first-child {margin:0;}
                            .form_text_type01 {position:relative;}
                            .btn_search_order {
                                position:absolute; top:50%; right:24px; background-image:url(../../images/common/search_icon.png);
                                @include textBackground(24px, 24px);
                                @include translate(0, -50%);
                                @include backgroundSoltC();
                            }
                        }
                    }
                    .btn_search {width:106px; font-size:18px; line-height:58px; border-radius:10px; position:absolute; bottom:0; right:0; font-weight:$bold; color:$white; background-color:$blue5;}
                }
            }
            .result_wrap {
                margin:35px 0 0;
                .result_box {
                    border:1px solid $gray2; border-radius:15px; margin:24px 0 0; position:relative;
                    &:first-child {margin:0;}
                    .title {
                        position:relative; background-color:$gray1; padding:15px 30px; border-radius:15px 15px 0 0;
                        input[type="checkbox"] {
                            position:absolute; top:0; left:0; z-index:-1; opacity:0;
                            &:focus ~ label:before {border-color:$blue5;}
                            &:hover ~ label:before {border-color:$blue5;}
                            &:checked ~ label:before {border:none; background-color:$blue5;}
                            &:checked ~ label:after {opacity:1;}
                            & + label {
                                display:inline-block; padding:0 0 0 50px; position:relative;
                                &:before, &:after {
                                    content:""; position:absolute; top:50%; left:0;
                                    @include translate(0, -50%);
                                }
                                &:before {width:20px; height:20px; border:1px solid $gray3; border-radius:4px; box-sizing:border-box; background-color:$white;}
                                &:after {
                                    width:20px; height:20px; display:block; opacity:0; background-image:url(../../images/common/check_icon.png);
                                    @include backgroundSoltC();
                                }
                                span {display:inline-block; margin:0 5px 0 0; vertical-align:middle; color:$gray5; font-size:14px; line-height:20px; }
                                strong {display:inline-block; vertical-align:middle; color:$gray7; font-size:18px; line-height:20px; font-weight:$medium;}
                            }
                        }
                    }
                    .info {
                        display:flex; align-items:center; justify-content:space-between; padding:25px 80px;
                        ul {
                            width:100%; display:flex; flex-wrap:wrap;
                            li {
                                margin:25px 0 0; box-sizing:border-box; display:flex; padding:0 0 0 20px;
                                &:nth-of-type(1), &:nth-of-type(4) {width:24%; padding:0;}
                                &:nth-of-type(2), &:nth-of-type(5) {width:38%;}
                                &:nth-of-type(3), &:nth-of-type(6) {width:38%;}
                            }
                            li:nth-of-type(1) {margin:0;}
                            span {font-size:14px; display:block; color:$gray5;}
                            p {font-size:16px; color:$gray7; margin:0 0 0 20px;}
                        }
                        .status {
                            position:absolute; top:14px; right:30px;
                            em {
                                font-size:12px; font-weight:$medium; padding:3px 13px; border-radius:20px;
                                &.warranty_type01 {color:$blue5; background-color:#dcebfe}
                                &.warranty_type02 {color:$red; background-color:#feecea}
                            }
                        }
                    }
                }
                .no_result {
                    text-align:center; font-size:16px; color:$gray5; padding:80px 0;
                }
            }
            .form_btn_wrap {padding:65px 0 0;}
        }
    }
    .btn_popup_close {width:20px; height:20px; top:28px; right:26px; background-image:url(../../images/common/close_x_btn.png);}
    @media screen and (min-width:1025px) {
        .popup_content {
            .cont {
                .result_wrap {
                    .result_box {
                        .info {
                            ul {
                                li:nth-of-type(2), li:nth-of-type(3) {margin:0;}
                                li:nth-of-type(3n+1) {clear:both;}
                            }
                        }
                    }
                }
            }
        }
    }
    @include mob() {
        .popup_content {
            max-width:100%; margin:0 em(30); padding:em(100) em(10) em(100) em(30); border-radius:em(24);
            > strong {font-size:em(48); font-weight:$bold; display:block; padding:0 0 em(60)}
            .cont {
                /*height:em(600);*/
                max-height:calc(70vh - 5rem - 2.7rem - 5rem);
                .search_wrap {
                    padding:0 0 em(60);
                    .tit_wrap {
                        margin:0 0 em(20);
                        strong {font-size:em(34); font-weight:$bold;}
                    }
                    .box {
                        .form_list {
                            &:after {content:""; display:block; clear:both;}
                            .form_date_type01 {
                                width:calc(50% - 0.25rem); float:left;
                                &:last-child {float:right;}
                            }
                        }
                    }
                    .box_multi {
                        margin:em(30) 0 0; padding:0;
                        .multi_wrap {
                            .box_s {
                                width:calc(50% - 0.25rem); margin:em(30) 0 0;
                                .btn_search_order {
                                    width:em(64); height:em(64); right:em(20); background-image:url(../../images/common/btn_search.png);
                                }
                                &:first-child {width:100%;}
                                &:last-child {float:right;}
                            }
                        }
                        .btn_search {width:100%; margin:em(30) 0 0; font-size:em(32); line-height:em(116); border-radius:em(10); position:static;}
                    }
                }
                .result_wrap {
                    margin:em(60) 0 0;
                    .result_box {
                        border-radius:em(30); margin:em(60) 0 0;
                        .title {
                            padding:em(30) em(40); border-radius:em(30) em(30) 0 0;
                            input[type="checkbox"] {
                                & + label {
                                    padding:0 0 0 em(60);
                                    &:before {width:em(40); height:em(40); border-radius:em(6);}
                                    &:after {width:em(40); height:em(40);}
                                    span {display:block; margin:0 0 em(5); font-size:em(26); line-height:em(26); }
                                    strong {display:block; font-size:em(32); line-height:em(32); font-weight:$bold;}
                                }
                            }
                        }
                        .info {
                            display:block; padding:em(100) em(40) em(40); position:relative;
                            ul {
                                li {
                                    width:100%; margin:em(20) 0 0; display:block; padding:0;
                                    &:nth-of-type(1), &:nth-of-type(4) {width:100%;}
                                    &:nth-of-type(2), &:nth-of-type(5) {width:100%;}
                                    &:nth-of-type(3), &:nth-of-type(6) {width:100%;}
                                }
                                span {font-size:em(26);}
                                p {font-size:em(32); line-height:em(44); margin:em(10) 0 0;}
                            }
                            .status {
                                top:em(30); left:0; right:0; padding:0 em(40); box-sizing:border-box; text-align:right;
                                em {
                                    font-size:em(24); font-weight:$bold; padding:em(10) em(30); border-radius:em(40);
                                }
                            }
                        }
                    }
                    .no_result {
                        font-size:em(32); padding:em(60) 0 0;
                    }
                }
                .form_btn_wrap {padding:em(100) 0 0;}
            }
        }
        .btn_popup_close {width:em(72); height:em(72); background-image:url(../../images/common/close_x_btn_m.png); right:em(22); top:em(22);}
    }
}
//NHA-COMMON-FREIGHT DAMAGE RMA-027, NHA-COMMON-REPAIR RMA-030, NHA-COMMON-WARRANTY RMA-027, NHA-COMMON-EXCHANGE RMA-032
.rma_submit {
    max-width:1200px; margin:0 auto; padding:50px 0 100px;
    .title_wrap {
        position:relative;
        strong {display:block; color:$gray7; font-size:26px; font-weight:$medium; margin:0 0 25px;}
        span {display:block; color:$gray7; font-size:20px; font-weight:$medium; margin:0 0 15px;}
        button {
            font-size:13px; line-height:36px; padding:0 20px; color:$gray7; position:absolute; top:0; right:0; font-weight:$medium; border:1px solid $gray2; border-radius:20px;
            @include transition();
            &:hover, &:focus {
                background-color:$gray7; color:$white;
            }
        }
    }
    .overview_wrap {
        .box {
            margin:50px 0 0;
            &:first-child {margin:0;}
            .form_list {
                ul {
                    li {
                        margin:8px 0 0;
                        &:first-child {margin:0;}
                        &.half_box {
                            @extend %clearfix;
                            > div {
                                float:left; width:calc(50% - 4px);
                                &:last-child {float:right;}
                            }
                        }
                    }
                }
            }
            .check_01 {margin:30px 0 0;}
            .list_item {
                width:calc(50% - 4px); padding:26px 30px 18px; border:1px solid $gray2; border-radius:8px; box-sizing:border-box; position:relative;
                > span {display:inline-block; font-size:12px; font-weight:$medium; color:$blue5; background-color:rgba(23, 120, 251, .15); line-height:22px; border-radius:11px; padding:0 13px; position:absolute; top:30px; right:20px;}
            }
            .name_area {
                @extend %clearfix;
                p {float:left; line-height:24px;}
                .name {font-size:16px; color:$gray7; font-weight:$medium;}
                .country {
                    font-size:14px; color:$gray5; position:relative; margin:0 0 0 10px; padding:0 0 0 13px;
                    &:before {content:"/"; position:absolute; top:0; left:0;}
                }
            }
            .address_area {
                padding:4px 0 0; text-align:left;
                p {font-size:14px; color:$gray5; line-height:32px;}
            }
        }
        .box_shipping {
            .title_wrap {width:calc(50% - 4px)}
        }
    }
    .detail_wrap {
        padding:40px 0 0; margin:50px 0 0; border-top:1px solid $gray2;
        .box {
            padding:45px 0 0; margin:50px 0 0; border-top:1px solid $gray2;
            &:first-child {border:none; padding:0; margin:0;}
            .select_only {max-width:510px;}
            .selected_info_wrap {
                margin:60px 0 0;
                li:first-child {margin:0;}
                .form_list {
                    li {margin:8px 0 0;}
                }
                .info_list {
                    margin:60px 0 0;
                    li {
                        font-size:14px; line-height:20px; margin:2px 0 0; color:$gray5; position:relative; padding:0 0 0 8px;
                        &:before {content:"·"; position:absolute; top:0; left:0;}
                    }
                }
            }
            .form_check_radio_type01 ul {padding:0;}
        }
        .installation_box {
            .box_s {
                margin:50px 0 0; border:1px solid $gray2; border-radius:15px;
                &:first-child {margin:0;}
                .title {
                    background-color:$gray1; padding:18px 30px; position:relative; border-radius:15px 15px 0 0;
                    strong {font-size:20px;}
                    .btn_wrap {
                        position:absolute; top:50%; right:30px; font-size:0;
                        @include translate(0, -50%);
                        button {
                            border-radius:100%; background-color:$white; margin:0 0 0 10px; box-sizing:border-box; border:1px solid $gray3;
                            &:first-child {margin:0;}
                            @include textBackground(32px, 32px);
                            @include backgroundSoltC();
                            &.btn_add_installation {background-image:url(../../images/my_menu/add_icon.png)}
                            &.btn_remove_installation {background-image:url(../../images/my_menu/remove_icon.png)}
                        }
                    }
                }
                .cont {
                    padding:50px 40px;
                    > div {
                        margin:45px 0 0;
                        &:first-child {margin:0;}
                    }
                    .title_wrap_s {
                        margin:0 0 35px; position:relative;
                        strong {font-size:18px; color:$gray5; display:block;}
                        button {
                            @include transition();
                            &.btn_order_item {
                                padding:0 20px; font-size:13px; line-height:36px; font-weight:$medium; position:absolute; top:-10px; right:0; color:$gray7; border-radius:20px; border:1px solid $gray3; z-index:1;
                                &:hover, &:focus {background-color:$gray7; border-color:$gray7; color:$white;}
                            }
                            &.choose_files {
                                display:block; border:1px solid $gray3; border-radius:19px; padding:0 20px; position:absolute; top:50%; right:0;
                                @include translate(0, -50%);
                                span {
                                    font-size:13px; color:$gray7; font-weight:$medium; line-height:36px; padding:0 16px 0 0; position:relative;
                                    @include transition(color, .2s);
                                    &:after {
                                        width:11px; height:10px; background-image:url(../../images/my_menu/order_download_ico.png); content:""; position:absolute; top:50%; right:0;
                                        @include transform(rotate(180deg) translate(0, 50%));
                                        @include transition(background-image, .2s);
                                    }
                                }
                                &:hover, &:focus {
                                    span {
                                        color:$blue5;
                                        &:after {background-image:url(../../images/my_menu/order_download_ico_hover.png);}
                                    }
                                }
                            }
                        }
                        &.title_wrap_s_only {
                            margin:0;
                            button {top:0;}
                        }
                    }
                    .data_wrap {
                        .form_wrap {
                            li {
                                margin:8px 0 0;
                                &:first-child {margin:0;}
                                &:after {content:""; display:block; clear:both;}
                                > div {
                                    float:right; width:calc(50% - 4px);
                                    &:first-child {float:left;}
                                }
                                &.long_box {
                                    .form_text_type01 {width:100%;}
                                    .form_textarea_type01 {width:100%;}
                                }
                                &.serial_box {
                                    position:relative;
                                    .form_text_type01 {
                                        width:668px;
                                        &:after {content:""; display:block; clear:both;}
                                        div {
                                            width:calc(100% - 112px); float:left;
                                            & ~ .correct_txt {
                                                font-size:12px; padding:0; position:absolute; top:50%; left:680px; display:block; color:#41ba41;
                                                @include translate(0, -50%);
                                            }
                                        }
                                        .btn_verify_chk {
                                            width:104px; font-size:18px; line-height:58px; border-radius:10px; font-weight:$medium; color:$white; background-color:$blue5; float:right;
                                            &.btn_disabled {background-color:#a2c9fd; cursor:default;}
                                        }
                                    }
                                }
                            }
                        }
                        .chk_wrap {
                            margin:0 0 35px;
                            dl {
                                margin:30px 0 0; font-size:0;
                                &:first-child {margin:0;}
                                dt, dd {display:inline-block; vertical-align:top;}
                                dt {font-size:16px; line-height:22px; color:$gray5;}
                                dd {
                                    font-size:0; margin:0 0 0 30px;
                                    .form_check_radio_default {
                                        display:inline-block; vertical-align:top; margin:0 30px 0 0;
                                        &:last-child {margin:0;}
                                    }
                                }
                            }
                        }
                        .btn_wrap_s {
                            position:absolute; bottom:-66px; right:0;
                            @extend %clearfix;
                            button {
                                border:1px solid $gray3; border-radius:19px; padding:0 20px;
                                &.btn_down {float:left;}
                                &.btn_del {float:left; margin:0 0 0 10px;}
