// NHA-COMMON-Troubleshooting-045
.listable_type01.troubleshooting_type {
    li {padding:15px 10px;}
    .list_wrap {
        width:100%;
        dl {
            padding:10px 10px 10px 25px; border-left:1px solid $gray2;
            @include boxSizing;
            &:first-of-type { border:none; }
            &.error_code_elm { width:13%; min-width:135px;}
            &.description_elm { width:50%;}
            &.solution_elm {
                width:33%;
                span {display:block;}
                em { margin:0 5px 0 0; }
            }
        }
    }
    @include mob() {
        li {padding:0;}
        .list_wrap {
            width:initial;
            dl {
                padding:0; border:none;
                &.error_code_elm { width:30%; min-width:30%; padding:0 0 em(40); }
                &.description_elm {
                    width:70%; border-left:em(2) solid $gray2; padding:0 0 em(40) em(25);
                    dd { line-height:em(36);}
                }
                &.solution_elm {
                    width:100%; border-top:em(2) solid $gray2; padding:em(40) 0 0;
                    em { margin:0 em(10) 0 0; }
                    dd { line-height:em(42);}
                }
            }
            dd {
                padding:em(15) em(15) 0 0;
            }
        }
    }
}
